@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500&family=Ubuntu:wght@300;400;500&display=swap);
:root {
    /* Color variables */
    --color-primary: #df3b3b;
    --color-primary-rgb: rgb(223, 59, 59, 0.2);
    --color-primary-dark: #8d2424;
    --color-grey: #2a2a2a;
    --color-grey-light-1: #9b9b9b;
    --color-grey-light-2: #e2e2e2;
    --color-grey-dark: #191919;
    --color-dark: #0f0f0f;
    --color-white: #fff;
    --color-black: #000;

    /* Font variables */
    --font-heading: 'Roboto Slab', serif;
    --font-text: 'Ubuntu', sans-serif;
}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    scroll-behavior: smooth;
    font-size: 62.5%; /* 1rem == 10px */
}

@media screen and (max-width: 900px) {
    html {
        font-size: 50%;
    }
}

body {
    background-color: #191919;
    background-color: var(--color-grey-dark);
    box-sizing: border-box;
    overflow-x: hidden;
}

.fade-in {
    -webkit-animation: fade-in .3s ease-out forwards;
            animation: fade-in .3s ease-out forwards;
}

.text-red {
    color: #df3b3b;
    color: var(--color-primary);
}

.text-uppercase {
    text-transform: uppercase;
}

@-webkit-keyframes slide-down {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
}

@keyframes slide-down {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
}

@-webkit-keyframes pulsate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes pulsate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@-webkit-keyframes background-change {
    0% {
        background-color: transparent;
        z-index: -1;
    }

    100% {
        background-color: rgba(0,0,0, .6);
        z-index: 1;
    }
}

@keyframes background-change {
    0% {
        background-color: transparent;
        z-index: -1;
    }

    100% {
        background-color: rgba(0,0,0, .6);
