.hamburger-container {
    position: relative;
    height: 3rem;
    width: 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    margin-right: 3rem;
}

.hamburger {
    width: 2rem;
    height: 2px;
    background-color: var(--color-white);
    cursor: pointer;
    transition: .2s ease-out;
}

.hamburger::before {
    content: '';
    width: 3rem;
    height: 2px;
    background-color: var(--color-white);
    position: absolute;
    top: 7px;
    transition: .2s ease-out;
}

.hamburger::after {
    content: '';
    width: 1.5rem;
    height: 2px;
    background-color: var(--color-white);
    position: absolute;
    bottom: 7px;
    transition: .2s ease-out;
}

.hamburger-container:hover .hamburger::before {
    width: 2.5rem;
}

.hamburger-container:hover .hamburger::after {
    width: 3rem;
}

